@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{overflow:hidden}html,body,#root,.app{height:100%}.app{background-color:#fff;position:relative}.app-content{position:relative;margin-top:50px;margin-bottom:50px;height:-webkit-fill-available}

.app-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.app-logo{-webkit-animation:app-logo-spin infinite 20s linear;animation:app-logo-spin infinite 20s linear}}@-webkit-keyframes app-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes app-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.not-a-button{display:inline-block;border:none;margin:0;text-decoration:none;background:none;color:#ffffff;font-family:sans-serif;font-size:1rem;line-height:1;cursor:pointer;text-align:center;-webkit-appearance:none;-moz-appearance:none}.not-a-button :hover,.not-a-button :focus{background:none}.not-a-button :focus{outline:1px solid #fff;outline-offset:-4px}.spacer{width:100%;position:relative}

.app-header{background-color:env(REACT_APP_BACKGROUND_COLOR);display:flex;flex-flow:column nowrap;align-items:flex-start;justify-content:flex-start;position:absolute;top:0;width:100%;color:white}

.nav-bar{background-color:#546de5;background-image:linear-gradient(62deg, #546de5 0%, #778beb 100%);display:flex;align-items:baseline;position:fixed;left:0;top:0;width:100%;min-height:50px;z-index:5;overflow:visible}.nav-bar>#nav-logo{display:inline-block;align-self:center;height:45px;min-height:45px;max-height:45px;margin-left:14px;margin-right:14px;box-sizing:content-box}.nav-bar>#nav-logo>img{height:45px;min-height:45px;max-height:45px}.nav-bar>.nav-bar-item{float:right;cursor:pointer;display:flex;flex-flow:column nowrap;position:relative}.nav-bar>.nav-bar-item>.nav-bar-drop-down{display:flex;flex-flow:column nowrap;justify-content:flex-start;align-items:flex-start;background-color:#546de5;background-image:linear-gradient(118deg, #546de5 0%, #778beb 100%);position:absolute;top:50px;z-index:6;overflow:visible;width:-webkit-max-content;width:-moz-max-content;width:max-content}.nav-bar>.nav-bar-item>.nav-bar-drop-down>.nav-bar-drop-down-item{min-width:100%;position:relative}.nav-bar>.nav-bar-item>.nav-bar-drop-down>.nav-bar-drop-down-item>.nav-bar-side-pane{display:flex;flex-flow:column nowrap;justify-content:flex-start;align-items:flex-start;background-color:#778beb;position:absolute;left:100%;top:0;z-index:7;width:-webkit-max-content;width:-moz-max-content;width:max-content}.nav-bar>.nav-bar-item>.nav-bar-drop-down>.nav-bar-drop-down-item>.nav-bar-side-pane>.nav-bar-side-item{min-width:100%}.nav-bar .nav-item-title{color:#fff;text-align:left;text-decoration:none;font-size:17px;min-height:50px;height:50px;max-height:50px;box-sizing:border-box;background-clip:border-box;border-left-width:14px;border-right-width:14px;border-style:solid;border-color:#00000000;display:flex;align-items:center}.nav-bar .nav-bar-item:hover,.nav-bar .nav-bar-item.active{background-color:rgba(255,255,255,0.333333)}.nav-bar .nav-bar-item:hover .nav-bar-item-title,.nav-bar .nav-bar-item.active .nav-bar-item-title{color:#fff}.nav-bar .nav-item :hover>.nav-item-title,.nav-bar .nav-item .active>.nav-item-title{background-color:rgba(255,255,255,0.333333);color:#fff}.nav-bar>#top-nav-menu{display:none;position:absolute;right:0;top:0;height:50px;flex-flow:row nowrap;align-items:center}@media screen and (max-width: 535px){.nav-bar{justify-content:space-between}.nav-bar *:not(:first-child){display:none}.nav-bar #top-nav-menu{display:flex}.nav-bar.responsive{flex-direction:column;align-items:flex-start}.nav-bar.responsive #nav-logo{margin-top:2.5px}.nav-bar.responsive *:first-child{align-self:flex-start}.nav-bar.responsive *:not(:last-child){display:inline;display:initial;width:inherit;float:left;text-align:right}}

.app-footer{position:fixed;left:0;bottom:0;width:-webkit-fill-available;color:white}.footer-message{display:flex;flex-flow:column nowrap;justify-content:center;width:-webkit-fill-available;min-height:50px;height:50px;max-height:50px;z-index:5;overflow:hidden;text-align:center;white-space:nowrap;background-color:#546de5;background-image:linear-gradient(62deg, #546de5 30%, #778beb 130%)}.footer-message>p{color:#ddd;margin:0 1em}


.about{display:flex;flex-flow:column nowrap;justify-content:flex-start;align-items:flex-start;padding:0 20px}.about p{margin-top:0}.about h1,.about h2{margin-bottom:0}




.canvas-container{height:-webkit-fill-available;overflow:hidden}.canvas-container.overflow,.mobile{overflow-x:hidden;overflow-y:scroll}

.control-bar{background-clip:border-box;background-color:#f7f7f7}.controls{display:flex;flex-flow:row wrap}.controls>.control-group{margin-left:10px}.controls>.control-group>.control-group{margin-top:10px}.control-group{display:flex;flex-flow:row wrap}.control-group>.control-group{display:flex;flex-flow:column wrap;justify-content:space-between;margin-top:1em}.control-group>.control-group>.control-group{display:flex;flex-flow:row nowrap;width:100%}.control-group>.control-group>.control-group>:first-child:nth-last-child(1),.control-group>.control-group>.control-group>:first-child:nth-last-child(1) ~ *{width:100%}.control-group>.control-group>.control-group>:first-child:nth-last-child(2),.control-group>.control-group>.control-group>:first-child:nth-last-child(2) ~ *{width:50%}.control-group>.control-group>.control-group>:first-child:nth-last-child(3),.control-group>.control-group>.control-group>:first-child:nth-last-child(3) ~ *{width:33.33333%}.control-group>.control-group>.control-group>:first-child:nth-last-child(4),.control-group>.control-group>.control-group>:first-child:nth-last-child(4) ~ *{width:25%}.control-group>.control-group>.control-group>:first-child:nth-last-child(5),.control-group>.control-group>.control-group>:first-child:nth-last-child(5) ~ *{width:20%}.control-container{margin:0;padding:1em;box-sizing:border-box;width:100%;background-clip:border-box;background-color:#f7f7f7}.control-container#main-control{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.control-container#default-control{position:fixed;bottom:50px}.control-label{margin:0;text-align:center;font-weight:bold;font-size:20px}input{-webkit-appearance:none;-moz-appearance:none;appearance:none;margin:0 10px;height:2em;padding:0 0.5em;border:none;border-radius:4px;border-style:solid;border-width:thin;border-color:#aaa;background-color:#fffd;-webkit-transition:box-shadow .15s ease-in-out, background-color .15s ease-in-out;transition:box-shadow .15s ease-in-out, background-color .15s ease-in-out}input:hover{background-color:#fffe;box-shadow:0px 4px 20px 0px rgba(0,0,0,0.1)}input:focus{outline:none;border-color:#546de5;background-color:#fff;box-shadow:0px 4px 20px 0px rgba(0,0,0,0.2)}


